ted;\r\n  cursor: help;\r\n}\r\n\r\nmark {\r\n  background: none;\r\n  color: inherit;\r\n}\r\n\r\nbutton,\r\ninput,\r\nselect,\r\ntextarea {\r\n  margin: 0;\r\n  font-size: 100%;\r\n  vertical-align: middle;\r\n  border: none;\r\n  cursor: pointer;\r\n  background:none;\r\n}\r\n\r\ninput[type=\"checkbox\"],\r\ninput[type=\"radio\"] {\r\n  box-sizing: border-box;\r\n  padding: 0;\r\n}\r\n\r\ntable {\r\n  border-collapse: collapse;\r\n  border-spacing: 0;\r\n}\r\n\r\nbody:not(:-moz-handler-blocked) {\r\n  -moz-outline-style: none;\r\n}\r\n\r\nhtml, body, div, span, applet, object, iframe,\r\nh1, h2, h3, h4, h5, h6, p, blockquote, pre,\r\na, abbr, acronym, address, big, cite, code,\r\ndel, dfn, em, img, ins, kbd, q, s, samp,\r\nsmall, strike, strong, sub, sup, tt, var,\r\nb, u, i, center,\r\ndl, dt, dd, ol, ul, li,\r\nfieldset, form, label, legend,\r\ntable, caption, tbody, tfoot, thead, tr, th, td,\r\narticle, aside, canvas, details, figcaption, figure,\r\nfooter, header, hgroup, menu, nav, section, summary,\r\ntime, mark, audio, video {\r\n  margin: 0;\r\n  padding: 0;\r\n  border: 0;\r\n  font-size: 100%;\r\n  font: inherit;\r\n  vertical-align: baseline;\r\n}\r\n\r\nbody {\r\n  line-height: 1;\r\n}\r\n\r\ntextarea {\r\n  overflow: auto;\r\n}\r\n\r\nhtml {\r\n  overflow-y: scroll;\r\n}\r\n","@import \"@libs/normalize\";\r\n@import \"@s-common/fonts\";\r\n@import \"@s-common/vars\";\r\n@import \"@s-common/mixins\";\r\n@import \"@s-modules/header\";\r\n@import \"@s-modules/discuss\";\r\n@import \"@s-modules/footer\";\r\n@import \"@s-pages/homepage\";\r\n\r\nbody {\r\n\twidth: 100%;\r\n\tmin-height: 100%;\r\n\tfont-family: \"Ubu-R\";\r\n\tuser-select: none;\r\n}\r\n//===================================\r\n//===================================\r\n.wrapper {\r\n\theight: 100%;\r\n\twidth: 100%;\r\n\tdisplay: grid;\r\n\tgrid-template: minmax(90px, auto) 1fr minmax(1fr, 825px) minmax(566px, auto) / 1fr;\r\n\tgrid-template-areas:\r\n\t\t\"header\"\r\n\t\t\"main\"\r\n\t\t\"discuss\"\r\n\t\t\"footer\";\r\n}\r\n//===================================\r\n//===================================\r\n.header {\r\n\tgrid-area: header;\r\n\tz-index: 1000;\r\n\tposition: sticky;\r\n\ttop: 0;\r\n\tbackdrop-filter: blur(3px);\r\n\tbackground-color: rgba($color: white, $alpha: 0.4);\r\n}\r\n.main {\r\n\tgrid-area: main;\r\n\tdisplay: flex;\r\n\tflex-direction: column;\r\n\tsection {\r\n\t\t.center {\r\n\t\t\tgrid-column: 2/3;\r\n\t\t}\r\n\t}\r\n}\r\n.discuss {\r\n\tgrid-area: discuss;\r\n}\r\n.footer {\r\n\tgrid-area: footer;\r\n\r\n\tbackground-color: $titles;\r\n}\r\n//===================================\r\n//===================================\r\n.parallax-block {\r\n\twidth: 100%;\r\n\theight: 100vh;\r\n\tposition: fixed;\r\n\ttop: 90px;\r\n\tz-index: -1;\r\n}\r\n.go-top {\r\n\tposition: fixed;\r\n\tdisplay: flex;\r\n\tcolumn-gap: 16px;\r\n\tbottom: 4%;\r\n\tright: 4%;\r\n\tbackground: none;\r\n\topacity: 0;\r\n\ttransition: all 0.5s ease;\r\n\tfont-weight: 600;\r\n\ttext-shadow: 0 0 4px rgba($color: $text, $alpha: 0.6);\r\n\tz-index: 100;\r\n\t&__text {\r\n\t\talign-self: flex-end;\r\n\t\tfont-size: 14px;\r\n\t\tcolor: $primary;\r\n\t\tline-height: 150%;\r\n\t\t@media (max-width: $desktop) {\r\n\t\t\tmargin: 10px 0 0;\r\n\t\t\talign-self: flex-end;\r\n\t\t\twriting-mode: vertical-lr;\r\n\t\t\tfont-size: 16px;\r\n\t\t}\r\n\t}\r\n\t&__squer {\r\n\t\twidth: 40px;\r\n\t\theight: 40px;\r\n\t\tborder-radius: 4px;\r\n\t\tbackground: $primary;\r\n\t\tposition: relative;\r\n\t\toverflow: hidden;\r\n\t\t&::before {\r\n\t\t\tcontent: \"\";\r\n\t\t\tposition: absolute;\r\n\t\t\twidth: 16px;\r\n\t\t\theight: 16px;\r\n\t\t\ttop: 65%;\r\n\t\t\tleft: 50%;\r\n\t\t\ttransform: translate(-50%, -50%) rotate(45deg);\r\n\t\t\tborder-left: 3px solid $text;\r\n\t\t\tborder-top: 3px solid $text;\r\n\t\t}\r\n\t\t@media (max-width: $desktop) {\r\n\t\t\twidth: 15px;\r\n\t\t\theight: 25px;\r\n\t\t\t&::before {\r\n\t\t\t\twidth: 10px;\r\n\t\t\t\theight: 10px;\r\n\t\t\t}\r\n\t